.test {
  z-index: 10;
}
.modalMenu {
  position: fixed;
  z-index: 10;
  padding: 0 16px 28px 16px;
  width: 100%;
  height: 100%;
  background-color: #fff;
  top: 0;
  left: 0;

  .top {
    padding: 26px 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #ECEFF2;
  
    .close {
      width: 18px;
      height: 18px;
    }
  }

  .menuItems {
    .item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;
      padding: 16px 0;
      border-bottom: 1px solid #ECEFF2;

      .title {
        font-size: 18px;
        color: #83898F;
      }
    }
  }
} 
