.step {
  display: grid;
  grid-template-columns: 56px 1fr;
  gap: 16px;

  @media screen and (min-width: 1920px){
    grid-template-columns: 1fr;
  }

  .text {
    text-align: left;
    line-height: 24px;

    .title {
      font-size: 16px;
      color: #191C1F;
      font-weight: 600;
    }

    .info {
      font-size: 14px;
      color: #5E646A;
    }
  }
}
